body {
  min-width: 320px;
  margin: 0;
}

.hidden {
  display: none;
}

.body-lock {
  width: 100%;
  overflow-y: scroll;
  position: fixed;
}

.wrapper {
  display: flex;
	flex-direction: column;
	min-height: 100%;
}

.content {
	flex: 1 0 auto;
}

.footer {
	flex: 0 0 auto;
}
